@-webkit-keyframes scales {
  0% {
    transform: scale(1.0);
    -webkit-transform: scale(1.0);
  }
  50% {
    transform: scale(1.5);
    -webkit-transform: scale(1.5);
  }
  100% {
      transform: scale(2.0);
      -webkit-transform: scale(2.0);
  }
}

@keyframes scales {
  0% {
    transform: scale(1.0);
    -webkit-transform: scale(1.0);
  }
  50% {
    transform: scale(1.5);
    -webkit-transform: scale(1.5);
  }
  100% {
      transform: scale(2.0);
      -webkit-transform: scale(2.0);
  }
}

